
.connection > div {
	margin-bottom: 10px;
}
.profileImgLink {
	float: left;
	margin-right: 10px;
}
.date-picker {
	width: 128px;
	margin: 0 auto !important;
}
.vue-star-rating {
	text-align: center;
	display: block !important;
}
.actions {
	text-align: center;
}
@media (max-width: 767px) {
.vue-star-rating > .vue-star-rating,
	.dueDate {
		text-align: left;
}
.actions {
		text-align: right;
}
}
@media (max-width: 750px) {
.vue-star-rating > .vue-star-rating,
	.dueDate,
	.actions {
		text-align: center;
}
}
.profileImg {
	width: 80px;
	border-radius: 80px;
}
.name > a {
	float: left;
	margin-right: 10px;
	color: #704b1e !important;
	font-size: 16px !important;
	font-weight: 900;
}
.title {
	color: #babfc2 !important;
	overflow: hidden !important;
	font-size: 13px !important;
}
.notes textarea {
	resize: none;
	width: 100%;
	height: 120px;
	font-size: 13px !important;
	background-color: #fbfbfb;
	border: 1px solid rgb(204, 204, 204);
	padding-top: 18px;
	padding-left: 10px;
}
::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	border-radius: 10px;
	background-color: #f5f5f5;
}
::-webkit-scrollbar {
	width: 10px;
	background-color: #f5f5f5;
}
::-webkit-scrollbar-thumb {
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	background-color: #555;
}
.input-wrapper > .input {
	line-height: 18px !important;
}
.date-panel {
	position: fixed !important;
	height: 320px !important;
	left: 0px !important;
	right: 0px !important;
	top: 0px !important;
	bottom: 0px !important;
	margin: auto !important;
}
.vue-input-tag-wrapper {
	background-color: #fbfbfb !important;
}
.vue-input-tag-wrapper > input {
	border: 0px !important;
	background-color: #fbfbfb !important;
	border-color: #ededed !important;
}
.input-tag {
	display: -webkit-box !important;
	display: -ms-flexbox !important;
	display: flex !important;
	-webkit-box-pack: center !important;
	    -ms-flex-pack: center !important;
	        justify-content: center !important;
	-webkit-box-align: center !important;
	    -ms-flex-align: center !important;
	        align-items: center !important;
	border-radius: 18px !important;
	border: 0px !important;
	padding: 2px 8px 2px 8px !important;
	line-height: 13px !important;
	background-color: #24c602 !important;
	color: #ffffff !important;
}
.input-tag span {
	line-height: 13px !important;
}
.input-tag a.remove {
	color: #ffffff !important;
	margin-left: 3px !important;
}
.actions > button {
	display: inline-block !important;
	color: #ffffff !important;
	font-size: 12px !important;
	padding: 6px 4px 6px 4px !important;
}
.doneBtn {
	margin-right: 4px;
	background-color: #24c602;
}
.doneBtn:hover {
	background-color: #1e8f08;
}
.snoozeBtn {
	background-color: #ffc107;
}
.snoozeBtn:hover {
	background-color: #e4ab00;
}

